<template>
  <div id="average">
    <el-tabs v-model="activeName">
      <el-tab-pane label="参与打分" name="1">
        <div class="content">
          <vote :activeName.sync="activeName"></vote>
        </div>
      </el-tab-pane>
      <el-tab-pane label="打分结果" name="2">
        <div class="content">
          <collect :activeName="activeName"></collect>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
const collect = resolve => require(['./components/collect'], resolve)
const vote = resolve => require(['./components/vote'], resolve)
export default {
  name: 'average',
  components: {
    vote,
    collect
  },
  data () {
    return {
      activeName: '1'
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss">
  #average {
    .content {
      padding: 0 .3rem;
    }
    .el-tabs__nav {
      width: 100%!important;
      text-align: center!important;
    }
    .el-tabs--top .el-tabs__item.is-top {
      width: 50%!important;
    }
  }
</style>



